import {  Box, Button, Center, Container, Heading, Image, Link, Text} from "@chakra-ui/react"
import { useRef } from "react"
import { useInView } from 'react-intersection-observer'
import GreenFunding from "./assets/sponsor/GREEN_FUNDING.png"


export const CrowdFunding = () => {
  const sectionTitleRef = useRef<HTMLDivElement>(null)
  const containerRef = useRef<HTMLDivElement>(null)
  const { ref, inView } = useInView(
    { 
      triggerOnce: true,
    }
  )
  if(inView) {
    sectionTitleRef.current?.classList.add("slide-from-left")
    containerRef.current?.classList.add("fade-in-slide")
    sectionTitleRef.current?.classList.remove("initial-hidden")
  }
  return(
    <Box mb="2rem" id="crowdfunding" ref={ref}>
      <Container maxW={["sm", "xl"]} mb="3rem" ref={ref}>
        <Heading as="h1" display="inline-block" bgColor="yellow" padding="1rem 3rem" fontWeight="medium" ref={sectionTitleRef} className="initial-hidden">
          Crowdfunding
        </Heading>
      </Container>
      <Box bgColor="black" color="white" fontSize="xs" fontWeight="thin" py="3rem" px="2rem" >
        <Container className="initial-hidden" ref={containerRef}>
          <Text>202333() 19:00~ <br/><br/>
          <Link color="yellow" href="https://greenfunding.jp" isExternal>GREEN FUNDING</Link><br/><br/>
          <br/>
          <br/><br/>
          Project </Text>
        </Container>
        <Center mt="2rem">
          <Button variant="ghost" p="0px" colorScheme="whiteAlpha" fontWeight="thin">
            <Center bgColor="white" color="blue" w="160px" h="40px" borderRadius="30px" position="relative">
              <Text fontFamily="Montserrat" textAlign="center">3/3 19:00 </Text>
            </Center>
          </Button>
        </Center>
      </Box>
    </Box>
  )
}